import * as React from 'react';
import { DiffEditor } from 'oned-react';

class DiffEditorDemo extends React.Component {
  render() {
    const original = 'select * from a';
    const modified = 'select * from b'
    return <div style={{height: 400}}>
      <DiffEditor data={[modified, original]} />
    </div>
  }
}

export default <DiffEditorDemo />;
