import * as React from 'react';
import { MonacoEditor } from 'oned-react';
import { CompletionItemKind } from '../define';

class MonacoEditorDemo extends React.Component {
  state = {
    value: 'select * from a',
    initialMark: [
      {
        message: '测试效果',
        startRow: 1,
        startCol: 1,
      },
    ],
  };

  onValueChange = value => {
    console.log(value);
    this.setState({
      initialMark: [],
    });
  };

  onEditorCreated = () => {
    console.log('created');
  };

  render() {
    const { value, initialMark } = this.state;
    return (
      <div style={{ height: 400 }}>
        <MonacoEditor
          value={value}
          onValueChange={this.onValueChange}
          onEditorCreated={this.onEditorCreated}
          // 提供mock数据
          query={{
            url: 'mock',
          }}
          options={{
            theme: `darkTheme`,
          }}
          language={'ODPSSQL'}
          marks={initialMark}
          completionTypes={{
            SAMPLE_TYPE_ONE: {
              text: '样例类型1',
              kind: CompletionItemKind.Method,
            },
            SAMPLE_TYPE_TWO: {
              text: '样例类型2',
              kind: CompletionItemKind.Variable,
            },
            KEYWORD: {
              text: "I18N.MonacoEditor.keywords",
              kind: CompletionItemKind.Keyword
            },
            CONSTS: {
              text: "I18N.MonacoEditor.define.consts",
              kind: CompletionItemKind.Snippet
            },
            FUNCTION: {
              text: "I18N.entity.function",
              kind: CompletionItemKind.Function
            },
            TABLE: {
              text: "I18N.physicalTable.column.tableName",
              kind: CompletionItemKind.Field
            },
            TABLEALIAS: {
              text: "I18N.MonacoEditor.define.tableAlias",
              kind: CompletionItemKind.Field
            },
            FIELD: {
              text: "I18N.MonacoEditor.define.field",
              kind: CompletionItemKind.Field
            },
            FIELDALIAS: {
              text: "I18N.MonacoEditor.define.fieldAlias",
              kind: CompletionItemKind.Field
            }
          }}
        />
      </div>
    );
  }
}

export default <MonacoEditorDemo />;

